<template>
    <div class="container">
        <div class="main__items">
            <div class="main__items-item" v-for="todo in todos" :key="todo.id">
                <h1 href="">Title: {{ todo.title}}</h1>
                <p>Completed: {{ todo.completed }}</p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                todos: ''
            }
        },
        created() {
            this.getTodos()
        },
        methods: {
            async getTodos() {
                let res = await fetch('https://jsonplaceholder.typicode.com/todos')
                let data = await res.json()
                this.todos = data
            }
        },

    }
</script>

